export const BG_COLOR = '#d7ccc8'
export const TEXT_COLOR = '#000000CC'
export const PRIMARY_COLOR = '#3f51b5'
export const SECONDARY_COLOR = '#5e35b1'
export const ERR_COLOR = '#f44336'
export const PAPER_COLOR = '#fafafa'

export const FORM_ID = 'frankie-form'


export const defualtTheme = (theme) => {
  return {
    textStyles: {
      group: {
        font: theme?.textStyles?.group?.font || 'Google Sans',
        size: theme?.textStyles?.group?.size || 26,
        color: theme?.textStyles?.group?.color || TEXT_COLOR
      },
      question: {
        font: theme?.textStyles?.question?.font || 'Google Sans',
        size: theme?.textStyles?.question?.size || 18,
        color: theme?.textStyles?.question?.color || TEXT_COLOR
      },
      text: {
        font: theme?.textStyles?.text?.font || 'Google Sans',
        size: theme?.textStyles?.text?.size || 14,
        color: theme?.textStyles?.text?.color || TEXT_COLOR
      }
    },
    palette: {
      type: 'light',
      primary: {
        main: theme?.primaryColor || PRIMARY_COLOR,
      },
      secondary: {
        main: SECONDARY_COLOR,
      },
      error: {
        main: ERR_COLOR,
      },
      background: {
        default: theme?.bgColor || BG_COLOR,
        paper: theme?.paperColor || PAPER_COLOR,
      },
    }
  }
}

export const defaultSurveyTheme = {
  textStyles: {
    group: {
      font: 'Google Sans',
      size: 26,
      color: TEXT_COLOR
    },
    question: {
      font: 'Google Sans',
      size: 18,
      color: TEXT_COLOR
    },
    text: {
      font: 'Google Sans',
      size: 14,
      color: TEXT_COLOR
    }
  },
  primaryColor: PRIMARY_COLOR,
  bgColor: BG_COLOR,
  paperColor: PAPER_COLOR
}

export const serveyRandomSetup = { code: 'Survey', rules: [{ title: "order_priority", key: "random", rules: ["randomize_groups", "prioritize_groups"] }] }

export const setupOptions = (type) => {
  switch (type) {
    case "group":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_questions", "prioritize_questions"] }
      ]
    case "text_display":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] }
      ]
    case "image_display":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] }
      ]
    case "video_display":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "video_options", key: "video_options", rules: ["audio_only", "loop"] }
      ]
    case "text":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["maxChars", "hint"] },
        { title: "validation", key: "validation", rules: ["validation_required", "validation_max_char_length", "validation_min_char_length", "validation_pattern", "validation_contains", "validation_not_contains"] }
      ]
    case "other_text":
      return [
        { title: "setup", key: "setup", rules: ["maxChars"] },
        { title: "validation", key: "validation", rules: ["validation_required", "validation_max_char_length", "validation_min_char_length", "validation_pattern", "validation_contains", "validation_not_contains"] }
      ]
    case "number":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["maxChars", "hint"] },
        { title: "validation", key: "validation", rules: ["validation_required", "validation_between", "validation_not_between", "validation_lt", "validation_lte", "validation_gt", "validation_gte", "validation_equals", "validation_not_equal"] }
      ]
    case "email":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["maxChars", "hint"] },
        { title: "validation", key: "validation", rules: ["validation_required", "validation_pattern_email", "validation_max_char_length", "validation_min_char_length"] }
      ]
    case "paragraph":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["minRows", "showWordCount", "hint"] },
        { title: "validation", key: "validation", rules: ["validation_required", "validation_max_word_count", "validation_min_word_count", "validation_contains", "validation_not_contains"] }
      ]
    case "file_upload":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["max_file_size", "allowed_file_types"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "signature":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "date_time":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["dateFormat", "fullDayFormat", "maxDate", "minDate"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "date":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["dateFormat", "maxDate", "minDate"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "time":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["fullDayFormat"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "scq":
      return [
        { title: "skip_logic", key: "skip_logic", rules: ["skip_logic"] },
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_options", "prioritize_options"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "image_scq":
      return [
        { title: "display", key: "display", rules: ["hideText", "columns", "imageAspectRatio", "spacing"] },
        { title: "skip_logic", key: "skip_logic", rules: ["skip_logic"] },
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_options", "prioritize_options"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "mcq":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_options", "prioritize_options"] },
        { title: "validation", key: "validation", rules: ["validation_min_option_count", "validation_max_option_count", "validation_option_count"] }
      ]
    case "ranking":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_options", "prioritize_options"] },
        { title: "validation", key: "validation", rules: ["validation_min_ranking_count", "validation_max_ranking_count", "validation_ranking_count"] }
      ]
    case "image_ranking":
      return [
        { title: "display", key: "display", rules: ["hideText", "columns", "imageAspectRatio", "spacing"] },
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_options", "prioritize_options"] },
        { title: "validation", key: "validation", rules: ["validation_min_ranking_count", "validation_max_ranking_count", "validation_ranking_count"] }
      ]
    case "image_mcq":
      return [
        { title: "display", key: "display", rules: ["hideText", "columns", "imageAspectRatio", "spacing"] },
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_options", "prioritize_options"] },
        { title: "validation", key: "validation", rules: ["validation_min_option_count", "validation_max_option_count", "validation_option_count"] }
      ]
    case "scq_array":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "order_priority", key: "random", rules: ["randomize_rows", "prioritize_rows", "randomize_columns", "prioritize_columns"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
    case "nps":
      return [
        { title: "show_hide", key: "relevance", rules: ["relevance"] },
        { title: "setup", key: "setup", rules: ["lower_bound_hint", "higher_bound_hint"] },
        { title: "validation", key: "validation", rules: ["validation_required"] }
      ]
  }
}